.am-accordion-content-box {
    padding: 10px;
    background-color: #f6f6f6;
}
.am-accordion-item {
    margin-top: 10px;
}
.qr-text {
    margin-left: 15px;
    text-align: left;
    width: calc(100% - 85px);
}
.qr-invite {
    color: rgba(0,0,0,.9);
    font-size: 16px;
    margin-bottom: 20px;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.copy-btn {
    border-radius: 7px;
    background: #fb7299;
    padding: 2px 10px;
    color: #fff;
    font-weight: 500;
}
.active {
    color: #fb7299;
}
.qr-time {
    margin-bottom: 5px;
}